<!--我的公司 myCompany-->
<template>
  <div>
    <label class="el-form-item__label" style="width: 120px;">
      公司头像
    </label>
    <el-upload
      class="avatar-uploader"
      action=""
      :show-file-list="false"
      :before-upload="beforeupload">
      <img v-if="src" :src="src" class="avatar">
      <i v-else class="el-icon-plus avatar-uploader-icon"></i>
    </el-upload>
    <el-form ref="form" :model="form"  :rules="rules"   label-width="120px">
      <el-form-item label="公司名称" prop="company_name">
        <el-input v-model="form.company_name"></el-input>
      </el-form-item>
      <el-form-item label="公司地址" prop="company_address">
        <el-input v-model="form.company_address"></el-input>
      </el-form-item>
      <el-form-item label="公司具体信息" prop="context">
        <el-input
          type="textarea"
          :rows="6"
          placeholder="请输入内容"
          v-model="form.context">
        </el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="onSubmit">保存修改</el-button>
      </el-form-item>
    </el-form>
    <el-popconfirm
    confirmButtonText='取消'
    cancelButtonText='确定'
    icon="el-icon-info"
    iconColor="red"
    title="确定要关闭公司吗"
    @onCancel="ce()"
    >
    <el-button slot="reference">停用公司</el-button>
    </el-popconfirm>
  </div>
</template>

<script>
import url from "../../common/api";
import { mapState } from 'vuex'
  export default {
    data() {
      return {
        form: {
          // user_id:28,
          company_name: '',
          company_address : '',
          context : '',
        },
        rules: {
          company_name: [
            { required: true, message: '请输入公司名称', trigger: 'blur' }
          ],
          company_address: [
            { required: true, message: '请输入公司地址', trigger: 'blur' }
          ],
          context: [
            { required: true, message: '请输入公司具体信息', trigger: 'blur' }
          ],
        },
        param:new FormData(),//表单要提交的参数
        src:"", //展示图片的地址
      };
    },
    methods: {
      //阻止upload的自己上传，进行再操作
      beforeupload(file) {
          //创建临时的路径来展示图片
          var windowURL = window.URL || window.webkitURL;
          this.src=windowURL.createObjectURL(file);
          this.param.append('company_icon', file);
          return false;
      },
      onSubmit(){//表单提交的事件
        this.$refs['form'].validate((valid) => {
          if (valid) {
            var form = this.form;
            console.log(form,'----------xxx')
            this.param.append('company_name', form.company_name);
            this.param.append('user_id', this.userInfo.user_id);
            this.param.append('company_id', form.company_id);
            this.param.append('company_address', form.company_address);
            this.param.append('context', form.context);
            console.log(this.param,'canshi')
            url.Peditcompany(this.param).then(res=>{
              this.$message(res.data.message[0]);
            }).catch(err=>{
            console.log(err,'-------编辑公司错误')
            })
          } else {
            console.log('error submit!!');
            return false;
          }
        });
      },
      ce(){//停用公司事件
        url.deactivecompany({
            "user_id": this.userInfo.user_id,
            "company_id":this.userInfo.u_company_id
        }).then(res=>{
            console.log(res,'----------停用公司')
        })
      },
      Fneditcompany(){
          url.editcompany({
           company_id:this.userInfo.u_company_id
          }).then(res=>{
              // console.log(res,'这是后去自己所属公司的查看')
              if(res.data.code[0]=='200'){
                let newcompany=res.data.data[0].fields
                newcompany.company_id=res.data.data[0].pk
                this.src=this.static+newcompany.company_icon
                this.form=newcompany
              }else{
                this.$message(res.data.message[0]);
              }

          })
      }
    },
    mounted(){
      // 获取我的公司数据
      this.Fneditcompany()
    },
    computed:{
      ...mapState(['userInfo','static'])
    }
  }
</script>
<style lang="stylus" scoped>
  >>>.avatar-uploader .el-upload {
    border: 1px dashed #d9d9d9;
    border-radius: 6px;
    cursor: pointer;
    position: relative;
    overflow: hidden;
  }
  >>>.avatar-uploader .el-upload:hover {
    border-color: #409EFF;
  }
  >>>.avatar-uploader-icon {
    font-size: 28px;
    color: #8c939d;
    width: 100px;
    height: 100px;
    line-height: 100px;
    text-align: center;
  }
  >>>.avatar {
    width: 100px;
    height: 100px;
    display: block;
  }
</style>


